@media screen and (min-width: 320px) {
  header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 15px;
    display: flex;
    flex-wrap: wrap;
  }
  header h2 {
    font-size: 1em;
  }
  header .user-cart-info {
    align-items: center;
    display: flex;
    justify-content: space-around;
    width: 100%;
  }
  header .user-cart-info * {
    margin-right: 20px;
  }
  header .user-cart-info input[type=button] {
    height: 40px;
  }
  header .user-cart-info .cart-button {
    border: none;
    background: none;
    position: relative;
  }
  header .user-cart-info .cart-button .cart-counter {
    background: lightgreen;
    bottom: 0;
    border-radius: 50%;
    margin: 0 20px 0 0;
    padding: 4px 6px;
    position: absolute;
    right: 5px;
  }
  header .user-cart-info .mini-avatar {
    border: 1px solid #000;
    border-radius: 50%;
    height: 40px;
    width: 40px;
  }
  header .user-cart-info .icon {
    height: 40px;
    width: 40px;
  }
  header .user-cart-info .display-none {
    display: none;
  }
}
@media screen and (min-width: 480px) {
  header {
    display: flex;
    align-items: center;
    margin: 15px;
    flex-wrap: wrap;
  }
  header h2 {
    font-size: 1.5em;
    margin-right: 50px;
  }
  header .user-cart-info {
    width: auto;
  }
  header .user-cart-info * {
    margin-right: 20px;
  }
  header .user-cart-info .mini-avatar {
    border: 1px solid #000;
    border-radius: 50%;
    height: 60px;
    width: 60px;
  }
  header .user-cart-info .icon {
    height: 60px;
    width: 60px;
  }
}
@media screen and (min-width: 992px) {
  header {
    justify-content: space-between;
  }
}
@media screen and (min-width: 1200px) {
  header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 15px;
  }
  header .user-cart-info {
    width: auto;
  }
  header .user-cart-info * {
    margin-right: 20px;
  }
  header .user-cart-info input[type=button] {
    height: auto;
  }
  header .user-cart-info .mini-avatar {
    border: 1px solid #000;
    border-radius: 50%;
    height: 60px;
    width: 60px;
  }
  header .user-cart-info .icon {
    height: 60px;
    width: 60px;
  }
}
hr {
  margin: 0;
}

img[alt=cart] {
  cursor: pointer;
}/*# sourceMappingURL=header.css.map */