.avatar-image {
  border: 1px solid #000;
  height: 160px;
  margin: 5%;
  width: 160px;
}

.signin-section {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-size: 1.5em;
}
.signin-section label {
  font-weight: bold;
}
.signin-section input {
  font-size: 0.8em;
  width: 200px;
}
.signin-section input::-moz-placeholder {
  text-align: center;
  color: #000;
}
.signin-section input::placeholder {
  text-align: center;
  color: #000;
}
.signin-section input[type=button] {
  font-weight: bold;
  margin-top: 15px;
}/*# sourceMappingURL=signin.css.map */