@media screen and (min-width: 320px) {
	table {
		th {
			padding: 5px 5px;
		}
	}
}
@media screen and (min-width: 480px) {
	table {
		th {
			padding: 5px 15px;
		}
	}
}
.cart-section {
	max-width: 1201px;
	height: 100%;
	width: 100%;
	position: relative;
	margin: auto;
	.container {
		display: flex;
		flex-direction: column;
		height: 50%;
		margin-left: 5%;
		transform: translate(0, 65px);
		width: 90%;
		.total-price {
			align-self: flex-end;
			font-size: 1.5em;
			font-weight: bold;
		}
	}
	.button-prchase {
		background-color: beige;
		border: 1px solid black;
		border-radius: 5px;
		padding: 15px;
		position: absolute;
		top: 2%;
		right: 5%;
	}
	.emptyCartContainer {
		display: flex;
		flex-direction: column;
		height: 50%;
		margin-left: 50%;
		transform: translate(-50%, 0);
		align-items: center;
		.cartSvg {
			height: 90%;
			max-width: 90%;
			width: auto;
		}
		h3 {
			font-size: 2.5em;
		}
	}
}
table {
	border: 1px solid black;
	border-collapse: collapse;
	width: 100%;
	tr {
		height: 50px;
	}
	th {
		height: 50px;
		text-align: start;
	}
}
tr {
	border: 1px solid black;
	border-collapse: collapse;
}
th {
	border: 1px solid black;
	border-collapse: collapse;
}
.button-delete {
	background-color: beige;
	border: 1px solid black;
	border-radius: 5px;
	padding: 10px 15px;
	top: 2%;
	right: 5%;
}
